/*   */
.slick-track {
  padding-top: 20px;
  padding-bottom: 36px;
}

/*  */
.slick-slide {
  transform: scale(1);
  box-shadow: 0 10px 10px 2px rgba($text-color, 0.9);
  transition: box-shadow 0.5s, transform 0.5s;

  &:hover,
  &:focus {
    transform: scale(1.04);
    box-shadow: 0 6px 6px 2px rgba($text-color, 0.6);
    transition: box-shadow 0.5s, transform 0.5s;
  }

  &:active {
    transform: scale(1.04);
    box-shadow: 0 6px 6px 2px rgba($text-color, 0.6);
    transition: box-shadow 0.5s, transform 0.5s;
  }
}

