.slick-arrow {
  position: absolute;
  top: 50%;
  width: 46px;
  height: 46px;
  border-radius: 50%;
  background-color: $text-color;
  border: none;
  color: transparent;
  transform: translateY(-50%);
  box-shadow: 10px 10px 10px 0 rgba($text-color, 0.5);

  &::after {
    position: absolute;
    content: "";
    width: 10px;
    height: 0;
    top: 50%;
    transform: translateY(-50%);
  }

  &:hover,
  &:focus {
    background-color: rgba($white-color, 0.5);
    box-shadow: 10px 10px 10px 0 rgba($text-color, 0.9);
    transition: all 0.5s ease 0s;
    outline: none;
  }

  &:active {
    box-shadow: 6px 6px 6px 0 rgba($text-color, 0.6);
    transition: all 0.1s ease 0s;
  }

  @media (max-width: 839.98px) {
    width: 36px;
    height: 36px;

    &::after {
      width: 6px;
    }
  }
}

.slick-arrow.slick-prev {
  left: 0;

  &::after {
    left: 11px;
    border-top: 8px solid transparent;
    border-bottom: 8px solid transparent;
    border-right: 18px solid $link-color;
  }

  &:hover::after,
  &:focus::after {
    border-right: 18px solid $text-color;
    transition: all 0.5s ease 0s;
  }

  &:active::after {
    border-right: 18px solid $link-color;
    transition: all 0.1s ease 0s;
  }

  @media (max-width: 839.98px) {
    &::after {
      left: 8px;
      border-right: 14px solid $link-color;
    }

    &:hover::after,
    &:focus::after {
      border-right: 14px solid $text-color;
    }

    &:active::after {
      border-right: 14px solid $link-color;
      transition: all 0.1s ease 0s;
    }
  }
}

.slick-arrow.slick-next {
  right: 0;

  &::after {
    right: 11px;
    border-top: 8px solid transparent;
    border-bottom: 8px solid transparent;
    border-left: 18px solid $link-color;
  }

  &:hover::after,
  &:focus::after {
    border-left: 18px solid $text-color;
    transition: all 0.5s ease 0s;
  }

  &:active::after {
    border-left: 18px solid $link-color;
    transition: all 0.1s ease 0s;
  }

  @media (max-width: 839.98px) {
    &::after {
      right: 8px;
      border-left: 14px solid $link-color;
    }

    &:hover::after,
    &:focus::after {
      border-left: 14px solid $text-color;
    }

    &:active::after {
      border-left: 14px solid $link-color;
      transition: all 0.1s ease 0s;
    }
  }
}

.slick-dots {
  @include list-reset;
  padding-top: 14px;
  display: flex;
  justify-content: center;
}

.slick-dots li {
  margin-left: 10px;
  margin-right: 10px;
}

.slick-dots li button {
  font-size: 0;
  color: transparent;
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background-color: $text-color;
  border: none;
  box-shadow: 3px 3px 10px 0 rgba($text-color, 0.5);

  &:hover,
  &:focus {
    background-color: $white-color;
    transition: all 0.5s ease 0s;
    outline: none;
  }
}

.slick-dots li.slick-active button {
  background-color: $link-color;
  transition: all 0.5s ease 0s;
  box-shadow: 3px 3px 8px 0 rgba($text-color, 0.9);
}
